    mCoNikkeDbl(id, parentId, toId) { // 16
      // 16
      let target = document.getElementById(id);
      let toElement = document.getElementById(toId);
      let idx = 0;
      let cnt = 0;
      if (target.parentNode.id.includes(parentId)) {
        // IDID
        toElement.childNodes.forEach(function(item){
          if(item.id != undefined && item.id.includes("nikke")) {
            if(item.id <= target.id) idx = cnt+1;
            cnt++;
          };
        });
        toElement.insertBefore(target, toElement.childNodes.item(idx)); // 1
        
        // style
        target.style.position = "relative";
        target.style.top = "0px";
        target.style.left = "0px";
        target.style.zIndex = 1;
        
        if (this.coSelTab == 3) {
          this.mSaveQf(this.sqSquadSelect); // 
        }
      }
    },

    mResetRf() { // 
        this.timeStart = new Date();
        this.timeLog = "";
        this.rapidTotal = 0;
        this.rapidSuccessPer = 0;
        this.rapidDelay = 0;
        this.rapidDelayPer = 0;
        this.rapidFail = 0;
        this.rapidFailPer = 0;
        this.isRapidDelay = false;
        this.isRapidFail = false;
        this.downSecSuccess = 0; // Down
        this.upSecSuccess = 0; // up
        this.downSecTotal = 0; // Down
        this.upSecTotal = 0; // up
        this.downSecAvg = 0; // Down
        this.upSecAvg = 0; // up
    },

    //startRapid() { // 
    //    this.isRapid=!this.isRapid;
    //    this.mResetRf;
    //    this.timeLog = "<br>down/up[ms]<br><br>";
    //},

    mRtDownTouch() { // 
      //console.log("mRtDownTouch")
      this.mRtDown();
      this.isTouch = true;
    },

    mRtDown() { // 
      //console.log("mRtDown")
      if(!this.isDown) {
        this.isDown = true;
        let timeEnd = new Date();
        let timeDiff = timeEnd.getTime() - this.timeStart.getTime();
        if(timeDiff < this.upSecMin) {
          this.timeLog = "  <mark style='background-color: #f18960;'>up:" + timeDiff + "ms</mark><br>" + this.timeLog;
          this.isRapidFail = true; // ON
        } else if(timeDiff > this.upSecMax) {
          this.timeLog = "  <mark>up:" + timeDiff + "ms</mark><br>" + this.timeLog;
          this.isRapidDelay = true; // ON
        } else {
          this.timeLog = "  up:" + timeDiff + "ms<br>" + this.timeLog;
          this.upSecSuccess = timeDiff; // up
        }
        this.timeStart = timeEnd;
      }
    },

    mRtUpTouch() { // 
      //console.log("mRtUpTouch")
      this.mRtUp();
      this.isTouch = true;
    },

    mRtUp() { // 
      //console.log("mRtUp")
        if(this.isDown) {
          this.isDown = false;
          let timeEnd = new Date();
          let timeDiff = timeEnd.getTime() - this.timeStart.getTime();
          if(timeDiff < this.downSecMin) {
            this.timeLog = "<mark style='background-color: #f18960;'>down:" + timeDiff + "ms</mark>" + this.timeLog;
            this.isRapidFail = true; // ON
          } else if(timeDiff > this.downSecMax) {
            this.timeLog = "<mark>down:" + timeDiff + "ms</mark>" + this.timeLog;
            this.isRapidDelay = true; // ON
          } else {
            this.timeLog = "down:" + timeDiff + "ms" + this.timeLog;
            this.downSecSuccess = timeDiff; // down
          }
          this.timeStart = timeEnd;
          
          // 
          this.rapidTotal++; // +1
          if(this.isRapidFail){
            this.rapidFail++; // +1
          } else if(this.isRapidDelay) {
            this.rapidDelay++; // +1
          } else {
            this.downSecTotal = this.downSecTotal + this.downSecSuccess; // down
            this.upSecTotal = this.upSecTotal + this.upSecSuccess; // up
          }
          this.isRapidDelay = false;
          this.isRapidFail = false;
          this.rapidDelayPer = Math.round(this.rapidDelay / this.rapidTotal * 100);
          this.rapidFailPer = Math.round(this.rapidFail / this.rapidTotal * 100);
          this.rapidSuccessPer = 100 - this.rapidDelayPer - this.rapidFailPer;
          //alert("this.downSecTotal=" + this.downSecTotal);
          //alert("this.this.rapidTotal - this.rapidDelay - this.rapidFail=" + this.rapidTotal - this.rapidDelay - this.rapidFail);
          this.downSecAvg = Math.round(this.downSecTotal / (this.rapidTotal - this.rapidDelay - this.rapidFail));
          this.upSecAvg = Math.round(this.upSecTotal / (this.rapidTotal - this.rapidDelay - this.rapidFail));
        }
    },

    mNonAccClick() { // 
      const matches1 = document.querySelectorAll(".accordionDiv");
      if(matches1[0].open) {
        this.isAccDivOpen = true;
        setTimeout(this.mNonAccDivClickDelay, 50); //false()v-bindtruefalse
      }

      const matches2 = document.querySelectorAll(".accordionCon");
      if(matches2[0].open) {
        this.isAccConOpen = true;
        setTimeout(this.mNonAccConClickDelay, 50); //false()v-bindtruefalse
      }
    },

    mNonAccDivClickDelay() { // 
      this.isAccDivOpen = false;
    },

    mNonAccConClickDelay() { // 
      this.isAccConOpen = false;
    },

    onKeyDown: function(e) { // 
      var active_element = document.activeElement;
      if(active_element){
        active_element.blur();
      }
    },

    /*filterChange:function(e) { // 
      //
      this.searchWord = e.target.value;

      //
      var _this = this; // forEachthisthis
      let reg1 = new RegExp("<mark>",'g');
      let reg2 = new RegExp("</mark>",'g');
      this.packs.forEach(function(pack){
        pack.content = pack.content.replace(reg1, "");
        pack.content = pack.content.replace(reg2, "");
      })
      if (e.target.value !== "") {
        this.packs.forEach(function(pack){
          let reg3 = new RegExp(e.target.value,'g');
          pack.content = pack.content.replace(reg3, "<mark>" + e.target.value + "</mark>");
        })
      }
    },*/

    mouseOverAction(index){ // 
      var _this = this;
      let buf = "";
      this.packs.forEach(function(pack){
        if(pack.id == index) {
          buf = pack.content;
        }
      })

      let reg1 = new RegExp("<mark>",'g');
      let reg2 = new RegExp("</mark>",'g');
      buf = buf.replace(reg1, "");
      buf = buf.replace(reg2, "");
      (buf.match("\\*")) ? this.tJewel = 'topics' : this.tJewel = '';
      (buf.match("\\*")) ? this.tTicketLuxury = 'topics' : this.tTicketLuxury = '';
      (buf.match("\\*")) ? this.tTicketRegular = 'topics' : this.tTicketRegular = '';
      (buf.match("\\*")) ? this.tMoldCompany = 'topics' : this.tMoldCompany = '';
      (buf.match("\\*")) ? this.tMoldHigh = 'topics' : this.tMoldHigh = '';
      (buf.match("\\*")) ? this.tMoldMiddle = 'topics' : this.tMoldMiddle = '';
      (buf.match("\\*")) ? this.tNikeSelect = 'topics' : this.tNikeSelect = '';
      (buf.match("\\*")) ? this.tCostume = 'topics' : this.tCostume = '';
      (buf.match("\\*")) ? this.tCaseCredit = 'topics' : this.tCaseCredit = '';
      (buf.match("\\*")) ? this.tCaseCore = 'topics' : this.tCaseCore = '';
      (buf.match("\\*")) ? this.tCaseBattle = 'topics' : this.tCaseBattle = '';
      (buf.match("1k\\*")) ? this.tCredit = 'topics' : this.tCredit = '';
      (buf.match("\\*")) ? this.tCore = 'topics' : this.tCore = '';
      (buf.match("\\*")) ? this.tManualSkill1 = 'topics' : this.tManualSkill1 = '';
      (buf.match("\\*")) ? this.tManualSkill2 = 'topics' : this.tManualSkill2 = '';
      (buf.match("\\*")) ? this.tManualSkill3 = 'topics' : this.tManualSkill3 = '';
      (buf.match("\\*")) ? this.tManualBurst1 = 'topics' : this.tManualBurst1 = '';
      (buf.match("\\*")) ? this.tManualBurst2 = 'topics' : this.tManualBurst2 = '';
      (buf.match("\\*")) ? this.tManualBurst3 = 'topics' : this.tManualBurst3 = '';
      (buf.match("\\*")) ? this.tManualAttribute = 'topics' : this.tManualAttribute = '';
      (buf.match("\\*")) ? this.tCompanyArms = 'topics' : this.tCompanyArms = '';
      (buf.match("\\*")) ? this.tCustomModule = 'topics' : this.tCustomModule = '';
      (buf.match("\\*")) ? this.tModuleHigh = 'topics' : this.tModuleHigh = '';
      (buf.match("\\*")) ? this.tModule = 'topics' : this.tModule = '';
      (buf.match("T9\\*")) ? this.tEquipmentT9 = 'topics' : this.tEquipmentT9 = '';
      (buf.match("T8\\*")) ? this.tEquipmentT8 = 'topics' : this.tEquipmentT8 = '';
      (buf.match("T7\\*")) ? this.tEquipmentT7 = 'topics' : this.tEquipmentT7 = '';
      (buf.match("VIP\\*")) ? this.tFavorability500 = 'topics' : this.tFavorability500 = '';
      (buf.match("\\*")) ? this.tFavorability100 = 'topics' : this.tFavorability100 = '';
      (buf.match("\\*")) ? this.tFavorability50 = 'topics' : this.tFavorability50 = '';
    },

    mCalc(){ // 
      var _this = this; // forEachthisthis
      
      this.$cookies.set('pJewel', this.pJewel, 0);
      this.$cookies.set('pTicketLuxury', this.pTicketLuxury, 0);
      this.$cookies.set('pTicketRegular', this.pTicketRegular, 0);
      this.$cookies.set('pMoldCompany', this.pMoldCompany, 0);
      this.$cookies.set('pMoldHigh', this.pMoldHigh, 0);
      this.$cookies.set('pMoldMiddle', this.pMoldMiddle, 0);
      this.$cookies.set('pNikeSelect', this.pNikeSelect, 0);
      this.$cookies.set('pCostume', this.pCostume, 0);
      this.$cookies.set('pCaseCredit', this.pCaseCredit, 0);
      this.$cookies.set('pCaseCore', this.pCaseCore, 0);
      this.$cookies.set('pCaseBattle', this.pCaseBattle, 0);
      this.$cookies.set('pManualSkill1', this.pManualSkill1, 0);
      this.$cookies.set('pManualSkill2', this.pManualSkill2, 0);
      this.$cookies.set('pManualSkill3', this.pManualSkill3, 0);
      this.$cookies.set('pManualBurst1', this.pManualBurst1, 0);
      this.$cookies.set('pManualBurst2', this.pManualBurst2, 0);
      this.$cookies.set('pManualBurst3', this.pManualBurst3, 0);
      this.$cookies.set('pManualAttribute', this.pManualAttribute, 0);
      this.$cookies.set('pCompanyArms', this.pCompanyArms, 0);
      this.$cookies.set('pCustomModule', this.pCustomModule, 0);
      this.$cookies.set('pModuleHigh', this.pModuleHigh, 0);
      this.$cookies.set('pModule', this.pModule, 0);
      this.$cookies.set('pEquipmentT9', this.pEquipmentT9, 0);
      this.$cookies.set('pEquipmentT8', this.pEquipmentT8, 0);
      this.$cookies.set('pEquipmentT7', this.pEquipmentT7, 0);
      this.$cookies.set('pFavorability500', this.pFavorability500, 0);
      this.$cookies.set('pFavorability100', this.pFavorability100, 0);
      this.$cookies.set('pFavorability50', this.pFavorability50, 0);

      this.packs.forEach(function(pack){
        pack.myPrice = _this.pJewel * pack.items.Jewel // 
                    + _this.pTicketLuxury * pack.items.TicketLuxury // 
                    + _this.pTicketRegular * pack.items.TicketRegular // 
                    + _this.pMoldCompany * pack.items.MoldCompany // 
                    + _this.pMoldHigh * pack.items.MoldHigh // 
                    + _this.pMoldMiddle * pack.items.MoldMiddle // 
                    + _this.pNikeSelect * pack.items.NikeSelect // 
                    + _this.pCostume * pack.items.Costume // 
                    + _this.pCaseCredit * pack.items.CaseCredit // 
                    + _this.pCaseCore * pack.items.CaseCore // 
                    + _this.pCaseBattle * pack.items.CaseBattle // 
                    + _this.pCaseCredit * pack.items.Credit * 1000 / _this.coRewardCredit // 1k
                    + _this.pCaseCore * pack.items.Core / _this.coRewardCore // 
                    + _this.pManualSkill1 * pack.items.ManualSkill1 // 
                    + _this.pManualSkill2 * pack.items.ManualSkill2 // 
                    + _this.pManualSkill3 * pack.items.ManualSkill3 // 
                    + _this.pManualBurst1 * pack.items.ManualBurst1 // 
                    + _this.pManualBurst2 * pack.items.ManualBurst2 // 
                    + _this.pManualBurst3 * pack.items.ManualBurst3 // 
                    + _this.pManualAttribute * pack.items.ManualAttribute // 
                    + _this.pCompanyArms * pack.items.CompanyArms // 
                    + _this.pCustomModule * pack.items.CustomModule // 
                    + _this.pModuleHigh * pack.items.ModuleHigh // 
                    + _this.pModule * pack.items.Module // 
                    + _this.pEquipmentT9 * pack.items.EquipmentT9 // T9
                    + _this.pEquipmentT8 * pack.items.EquipmentT8 // T8
                    + _this.pEquipmentT7 * pack.items.EquipmentT7 // T7
                    + _this.pFavorability500 * pack.items.Favorability500 // VIP
                    + _this.pFavorability100 * pack.items.Favorability100 // 
                    + _this.pFavorability50 * pack.items.Favorability50 // 
                    + Math.max(_this.pCaseCredit * 3
                              , _this.pCaseCore
                              , _this.pCaseBattle
                              ) * pack.items.SupplyCG1 // 
                              
                    + Math.max(_this.pManualSkill1 * 9
                              , _this.pManualSkill2 * 7
                              , _this.pManualSkill3 * 3
                              ) * pack.items.ManualSkillSelect // 

                    + Math.max(_this.pManualBurst1 * 9
                              , _this.pManualBurst2 * 7
                              , _this.pManualBurst3 * 3
                              ) * pack.items.ManualBurstSelect // 

                    + Math.max(_this.pCaseCredit * 30
                              , _this.pCaseCore * 10
                              , _this.pCaseBattle * 10
                              , _this.pManualSkill1 * 28
                              , _this.pManualSkill2 * 20
                              , _this.pManualSkill3 * 8
                              , _this.pManualBurst1 * 14
                              , _this.pManualBurst2 * 10
                              , _this.pManualBurst3 * 4
                              ) * pack.items.Supply30Day // 30-DAY

                    + Math.max(_this.pCaseCredit * 25 + _this.pCaseCore * 25 + _this.pCaseBattle * 25
                              , _this.pCaseCredit * 150
                              , _this.pCaseCore * 50
                              , _this.pCaseBattle * 50
                              ) * pack.items.CustomPack1 // (1)
                    + Math.max(_this.pManualSkill1 * 200
                              , _this.pManualSkill2 * 140
                              , _this.pManualBurst1 * 100
                              , _this.pManualBurst2 * 70
                              ) * pack.items.CustomPack1 // (2)
                    
                    + Math.max(_this.pCompanyArms * 200
                              , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                              , _this.pCaseCredit * 300
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              ) * pack.items.CustomPack2 // (1)
                    + Math.max(_this.pCustomModule * 2
                              , _this.pManualSkill1 * 250
                              , _this.pManualSkill2 * 180
                              , _this.pManualBurst1 * 125
                              , _this.pManualBurst2 * 90
                              ) * pack.items.CustomPack2 // (2)
                    + Math.max(_this.pManualSkill1 * 360
                              , _this.pManualSkill2 * 250
                              , _this.pManualSkill3 * 100
                              , _this.pManualBurst1 * 180
                              , _this.pManualBurst2 * 125
                              , _this.pManualBurst3 * 50
                              ) * pack.items.CustomPack2 // (3)
                    
                    + Math.max(_this.pCompanyArms * 200
                              , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                              , _this.pCaseCredit * 300
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              ) * pack.items.CustomPack3 // (1)
                    + Math.max(_this.pCustomModule * 3
                              , _this.pManualSkill1 * 300
                              , _this.pManualSkill2 * 220
                              , _this.pManualBurst1 * 150
                              , _this.pManualBurst2 * 110
                              ) * pack.items.CustomPack3 // (2)
                    + Math.max(_this.pManualSkill1 * 360
                              , _this.pManualSkill2 * 250
                              , _this.pManualSkill3 * 100
                              , _this.pManualBurst1 * 180
                              , _this.pManualBurst2 * 125
                              , _this.pManualBurst3 * 50
                              ) * pack.items.CustomPack3 // (3)
                    + Math.max(_this.pTicketRegular * 30
                              , _this.pEquipmentT9 * 1
                              ) * pack.items.CustomPack3 // (3)
                    ;

        pack.degree = pack.myPrice / pack.price * 100;  // 
      })
    },

    mReset(){ // 
      var _this = this;
      this.divisions.forEach(function(division){
        division.check = false;
      });
      this.contents.forEach(function(content){
        content.check = false;
      });
    },

    mSortBy(key1, key2) {
      this.sort_key1 === key1
      ? (this.sort_asc = !this.sort_asc)
      : (this.sort_asc = true)
      this.sort_key1 = key1
      this.sort_key2 = key2
    },

    mAddClass(key) {
      return {
        asc: this.sort_key1 === key && this.sort_asc,
        desc: this.sort_key1 === key && !this.sort_asc
      }
    }
  },

  computed: {
    packs_sort() { // 
      var _this = this; // forEachthisthis

      // 
      if(this.sort_key1 != "") {
        let set = 1;
        this.sort_asc ? (set = 1) : (set = -1)
        this.packs.sort((a, b) => {
          //if (a[this.sort_key2] > b[this.sort_key2]) return -1;
          //if (a[this.sort_key2] < b[this.sort_key2]) return 1;
          if (a[this.sort_key1] < b[this.sort_key1]) return -1 * set;
          if (a[this.sort_key1] > b[this.sort_key1]) return 1 * set;
          return 0;
        })
      }

      // 
      var data = _this.packs;

      //
      let flgDivAll = true;
      _this.divisions.forEach(function(division){
        if(division.check){
          flgDivAll = false;
        }
      })
      if(!flgDivAll) {
        _this.divisions.forEach(function(division){
          if(!division.check){
            data = data.filter(function (row) {
              return row['division'].indexOf(division.name) == -1;
            });
          }
        })
      }

      //
      let flgConAll = true;
      _this.contents.forEach(function(content){
        if(content.check){
          flgConAll = false;
        }
      })
      if(!flgConAll) {
        data = data.filter(function (row) {
          let flgFing = false;
          _this.contents.forEach(function(content){
            if(content.check){
              if(row['content'].indexOf(content.name) >= 0) {
                flgFing = true
              }
            }
          });
          return flgFing;
        });
      }

      //
      let reg1 = new RegExp("<mark style='background-color: #f7f463;'>",'g');
      let reg2 = new RegExp("<mark style='background-color: #f18960;'>",'g');
      let reg3 = new RegExp("<mark style='background-color: #86ff40;'>",'g');
      let reg4 = new RegExp("<mark style='background-color: #659bff;'>",'g');
      let reg5 = new RegExp("<mark style='background-color: #f742c9;'>",'g');
      let regE = new RegExp("</mark>",'g');
      let num = 1;
      _this.packs.forEach(function(pack){
        pack.content = pack.content.replace(reg1, "");
        pack.content = pack.content.replace(reg2, "");
        pack.content = pack.content.replace(reg3, "");
        pack.content = pack.content.replace(reg4, "");
        pack.content = pack.content.replace(reg5, "");
        pack.content = pack.content.replace(regE, "");
      })
      _this.contents.forEach(function(content){
        if (content.check) {
          let reg3 = new RegExp(content.name,'g');
          _this.packs.forEach(function(pack){
            
            switch(num){
              case 1:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f7f463;'>" + content.name + "</mark>");
                break;
              case 2:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f18960;'>" + content.name + "</mark>");
                break;
              case 3:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #86ff40;'>" + content.name + "</mark>");
                break;
              case 4:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #659bff;'>" + content.name + "</mark>");
                break;
              default:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f742c9;'>" + content.name + "</mark>");
            }
          })
          if(++num > 5){
            num = 1;
          }
        }
      })

      return data
    }
  },

  destroyed: function () { // PT
    window.removeEventListener('dragstart', function(e){}, false)
    window.removeEventListener('drag', function(e){}, false)
    window.removeEventListener('dragover', function(e){}, false)
    window.removeEventListener('drop', function(e){}, false)
    //clearInterval(this.updateMessageTimer)
  }

}
</script>


